import React from 'react';
import { graphql } from "gatsby";
import { Disqus, CommentCount } from 'gatsby-plugin-disqus'
import Helmet from 'react-helmet'

import Layout from '../components/layout';

export const query = graphql`
    query ($slug: String) {
    markdownRemark(fields: {
        slug: {
            eq: $slug
        }
    }) {
        frontmatter {
            date(formatString: "MMMM Do, YYYY")
            title
            tags
        }
        html
    }
    }`;



const Post = (props) => {

    let disqusConfig = {
        url: `${'https://sirwan.info' + window.location.pathname}`,
        identifier: props.data.markdownRemark.frontmatter.slug,
        title: props.data.markdownRemark.frontmatter.title,
    }

    return <Layout>
        <Helmet>
            <title>{props.data.markdownRemark.frontmatter.title}</title>
        </Helmet>
        <div className="post">
            <h1>{props.data.markdownRemark.frontmatter.title}</h1>
            <span className="post-date">{props.data.markdownRemark.frontmatter.date} | <CommentCount config={disqusConfig} placeholder={'...'} /></span>
            <div dangerouslySetInnerHTML={{ __html: props.data.markdownRemark.html }}></div>
        </div>
        <Disqus config={disqusConfig} />
    </Layout>;
};

export default Post;
