import React from 'react';
import { Link, graphql, useStaticQuery } from 'gatsby';

const Header = () => {
    const data = useStaticQuery(graphql`query {
        site {
          siteMetadata {
            author
            description
            email
            title
            twitter_username
            url
          }
        }
      }
      `);

    return (<header className="masthead">
        <div className="masthead-inner">
            <div className="profilepic">
                <Link to="/"><img src="/avatar.png" alt="Profile Picture" /></Link>
            </div>
            <h2>{data.site.siteMetadata.author}</h2>
            <p className="lead">{data.site.siteMetadata.description}</p>

            <div className="colophon">
                <ul className="colophon-links">
                    <li>
                        <Link to="/about">about</Link>
                    </li>
                    <li>
                        <Link to="/archive">archives</Link>
                    </li>
                    <li>
                        <Link to="#">contact</Link>
                    </li>
                </ul>
                <p>© 2019. {data.site.siteMetadata.author} All rights reserved.</p>
            </div>
        </div>
    </header>)
};

export default Header;