import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';

const Footer = () => {
    const data = useStaticQuery(graphql`query {
        site {
          siteMetadata {
            author
          }
        }
      }
      `);

    return <footer id="footer" className="inner">
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <p>
                        Copyright © 2019 - {data.site.siteMetadata.author} | Powered by Gatsby |  Site design based on the <a href="http://hyde.getpoole.com/">Hyde theme</a>
                    </p>
                </div>
            </div>
        </div>
    </footer>
};

export default Footer;